import { PanelPlugin } from '@grafana/data';
import { config } from '@grafana/runtime';
import { SimpleOptions } from './types';
import { BoxPlotPanel } from './BoxPlotPanel';

export const plugin = new PanelPlugin<SimpleOptions>(BoxPlotPanel).setPanelOptions(builder => {
  return builder
    .addRadio({
      path: 'seriesOrientation',
      defaultValue: 'horizontal',
      name: 'Series orientation',
      settings: {
        options: [
          {
            value: 'horizontal',
            label: 'Horizontal',
          },
          {
            value: 'vertical',
            label: 'Vertical',
          },
        ],
      },
    })
    .addBooleanSwitch({
      path: 'gridLines',
      name: 'Show Grid Lines',
      defaultValue: false,
      category: ['Axis'],
    })
    .addNumberInput({
      path: 'numTicks',
      name: 'Number of Ticks',
      defaultValue: 10,
      category: ['Axis'],
    })
    .addBooleanSwitch({
      path: 'autoscale',
      name: 'Autoscale',
      defaultValue: true,
      category: ['Axis'],
    })
    .addNumberInput({
      path: 'minScale',
      name: 'Minimum',
      defaultValue: 0,
      showIf: (config: SimpleOptions) => !config.autoscale,
      category: ['Axis'],
    })
    .addNumberInput({
      path: 'maxScale',
      name: 'Maximum',
      defaultValue: 100,
      showIf: (config: SimpleOptions) => !config.autoscale,
      category: ['Axis'],
    })
    .addNumberInput({
      path: 'lineWidth',
      name: 'Line Width',
      defaultValue: 2,
    })
    .addColorPicker({
      path: 'boxColor',
      name: 'Box Color',
      defaultValue: 'green',
    })
    .addColorPicker({
      path: 'medianColor',
      name: 'Median Color',
      defaultValue: config.theme.isDark ? 'black' : 'white',
    })
    .addColorPicker({
      path: 'limitLineColor',
      name: 'Limit Line Color',
      defaultValue: config.theme.isDark ? 'white' : 'black',
    })
    .addSelect({
      path: 'boxStat',
      defaultValue: 'quartiles',
      name: 'Box Edges',
      category: ['Statistics'],
      settings: {
        options: [
          {
            value: 'quartiles',
            label: '1st and 3rd Quartile',
          },
          {
            value: 'oneSigma',
            label: 'Mean +/- 1 Standard Deviation',
          },
          {
            value: 'twoSigma',
            label: 'Mean +/- 2 Standard Deviation',
          },
          {
            value: 'threeSigma',
            label: 'Mean +/- 3 Standard Deviation',
          },
        ],
      },
    })
    .addSelect({
      path: 'whiskerStat',
      defaultValue: 'minMaxNonOutliers',
      name: 'Whisker Placement',
      category: ['Statistics'],
      settings: {
        options: [
          {
            value: 'minMaxNonOutliers',
            label: 'Min/Max of Non-Outliers',
          },
          {
            value: 'iqr',
            label: 'Box +/- 1.5 x Interquartile Range',
          },
          {
            value: 'percentile10',
            label: '10th/90th Percentile',
          },
          {
            value: 'std',
            label: 'Mean +/- 1 Standard Deviation',
          },
          {
            value: '2std',
            label: 'Mean +/- 2 Standard Deviation',
          },
          {
            value: '3std',
            label: 'Mean +/- 3 Standard Deviation',
          },
        ],
      },
    })
    .addBooleanSwitch({
      path: 'showMean',
      name: 'Show Mean',
      defaultValue: true,
      category: ['Statistics'],
    })
    .addBooleanSwitch({
      path: 'showMedian',
      name: 'Show Median',
      defaultValue: false,
      category: ['Statistics'],
    })
    .addBooleanSwitch({
      path: 'showOutliers',
      name: 'Show Outliers',
      defaultValue: true,
      category: ['Statistics'],
    })
    .addBooleanSwitch({
      path: 'showTarget',
      name: 'Show Target',
      defaultValue: false,
      category: ['Statistics'],
    })
    .addNumberInput({
      path: 'target',
      name: 'Target',
      defaultValue: 10,
      showIf: (config: SimpleOptions) => config.showTarget,
      category: ['Statistics'],
    })
    .addBooleanSwitch({
      path: 'showLimits',
      name: 'Show Limits',
      defaultValue: false,
      category: ['Statistics'],
    })
    .addNumberInput({
      path: 'controlLimit',
      name: 'Control (TGT +/-)',
      defaultValue: 10,
      showIf: (config: SimpleOptions) => config.showLimits,
      category: ['Statistics'],
    })
    .addNumberInput({
      path: 'warningLimit',
      name: 'Warning (TGT +/-)',
      defaultValue: 10,
      showIf: (config: SimpleOptions) => config.showLimits,
      category: ['Statistics'],
    });
});
